@use '../helpers/' as *;

.slider {
    width: 100%;
    display: flex;
    flex-direction: column;

    &__top {
      // margin-bottom: fluid(40, 20);
      margin-bottom: rem(20);
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__title {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(20);

      &-link {
        width: fluid(90, 80);
        height: fluid(40, 30);
        display: flex;
        justify-content: center;
        align-items: center;

        @include hover {
            .slider__title-link-icon {
                opacity: 0.6;
            }
        }

        &-icon {
            transition: opacity (--transition-duration) ease-in-out;
        }
      }
    }

    &__container {
      display: flex;
      flex-direction: column;
      row-gap: rem(20);
    }

    &__content {
      width: 100%;
      height: auto !important;
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      display: flex;
      // column-gap: rem(100);
      column-gap: fluid(100, 60);
      background-color: var(--background-additional);
      border: var(--border);
      border-radius: var(--radius);
      transition: var(--transition-with-border);
      user-select: none;
      // Фиксируем рендер на iPhone
      backface-visibility: hidden;
      // -webkit-backface-visibility: hidden;
      transform: translateZ(0);
      // -webkit-transform: translateZ(0);

      @include hover {
        border-color: var(--border-hover);
        background-color: var(--background-basic);
      }

      @include laptop {
        column-gap: fluid(60, 40);
      }

      @include mobile {
        height: 100%;
        flex-direction: column;
        column-gap: 0;
      }
    }

    &__nav {
      flex: 1;               
      max-width: 33.33%;        
      display: flex;
      flex-direction: column;
      row-gap: fluid(30, 20);

      @include mobile {
        flex: none;
        padding-bottom: fluid(60, 40);
        max-width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: rem(40);
      }
    }

    &__item {
      display: flex;
      align-items: center;
      gap: rem(4);
      cursor: pointer;
      transition: transform var(--transition-duration);
      
      &--active {
        pointer-events: none;
        .slider__dot {
          background-color: var(--elements-accent);
          transform: scale(1.4);
        }
        .slider__btn {
          color: var(--text);
          transform: scale(1.05);
        }
      }

      // &--focused {
      //   outline: 2px solid var(--text-additional);
      //   outline-offset: 6px;
      //   border-radius: 2px;
      //   transition-duration: 0s !important;

      //   &:focus-visible {
      //     outline: none;
      //   }
      // }

      @include hover {
        .slider__dot {
          background-color: var(--elements-accent);
          transform: scale(1.4);
        }
        .slider__btn {
          color: var(--text-basic); 
          transform: translateX(1rem) scale(1.2);
        }
      }
    }

    &__dot {
        @include square(rem(12));
        border-radius: 50%;
        background-color: var(--text-additional);
        margin-right: fluid(20, 10);
        transition: background-color 0.3s ease-in-out, transform 0.4s ease-in-out;
        will-change: transform;
      }

    &__btn {
        transition: color 0.3s ease-in-out,transform 0.4s ease-in-out;
        color: var(--text-additional);
        will-change: transform;
    }
  

    &__screens {
        flex: 2;
        max-width: 66.66%;
        display: flex;
        overflow: hidden;
        position: relative;
        backface-visibility: hidden;
        transform: translateZ(0);
    
        @include mobile {
            max-width: 100%;
            flex: none;
            flex-direction: column;
        }

        .swiper-wrapper {
            display: flex;
            align-items: stretch;
        }

        .swiper-slide {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            opacity: 0;
            transition: opacity 0.6s ease, transform 0.6s ease;
            will-change: opacity, transform;

        &.swiper-slide-active {
                opacity: 1;
            }
        }

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      
      background: var(--background-form-field);
      border-radius: var(--radius);
      border: var(--border-form);
      box-shadow: var(--shadow);
      
      transition: opacity 0.6s ease;

        &.swiper-slide-active {
          opacity: 1;
      }
    }

    &-description {
      align-self: flex-start;
      margin-bottom: 0;
      padding-bottom: fluid(20, 10);
      position: relative;
      flex-grow: 1;
      // font-size: fluid(16, 14);
      font-size: rem(14);
      opacity: 0;
      transform: translateY(rem(10));
      transition: opacity 0.6s ease, transform 0.6s ease;

      .swiper-slide-active & {
        opacity: 1;
        transform: translateY(0);
      }

      &::after {
        content: '';
        width: 100%;
        height: rem(2);
        position: absolute;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }

    &-image {
      margin-top: auto;
      padding-top: fluid(40, 20);
      display: flex;
      justify-content: center;
      align-items: flex-end;
      position: relative;
      
      opacity: 0;
      transform: translateY(rem(20));
      transition: opacity 0.6s ease, transform 0.6s ease;

      .swiper-slide-active & {
        opacity: 1;
        transform: translateY(0);
      }
      
      img {
        width: rem(520);
        max-width: 100%;
        border-radius: var(--radius);
        object-fit: contain;
        backface-visibility: hidden;
      }

      &--mobile {
        img {
          width: rem(180);
        }
      }
    }
  }
}