@use '../helpers/' as *;

.clients {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include laptop {
      width: 100%;
    }

    @include mobile {
      row-gap: fluid(60, 40);
    }
  }

  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: fluid(100, 60);
    margin-bottom: rem(60);

    @include laptop {
      column-gap: fluid(60, 40);
      margin-bottom: rem(20);
    }

    @include mobile {
      grid-template-columns: 1fr;
      grid-template-columns: 100%;
      margin-bottom: 0;
    }
  }

  &__text-slider {
    &-wrapper {
      width: 100%;
      display: flex;
      position: relative;
      overflow: hidden;
    }

    &-item {
      display: flex;
      flex-direction: column;
      row-gap: fluid(40, 20);
      transition: opacity 1s ease-in-out;
    }
  }

  &__slider {
    width: 100%;
    aspect-ratio: 5 / 3;
    position: relative;
    overflow: hidden;

    @include mobile-s {
      aspect-ratio: 4 / 3;
    }
  }

  &__video-container {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: var(--background-basic);
    overflow: hidden;
    z-index: 0;
  }

  &__video {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
    pointer-events: none;
    z-index: 1;
    mix-blend-mode: lighten;
    transform: translateZ(0);
    backface-visibility: hidden;

    &::-webkit-media-controls,
    &::-webkit-media-controls-start-playback-button {
      display: none !important;
      -webkit-appearance: none;
    }
  }

  &__slider-wrapper {
    position: absolute;
    inset: 0;
    z-index: 10;
    isolation: isolate;
  }

  &__slider-list {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__slider-item {
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.4;
    transition: opacity 0.5s var(--slide-transition),
                transform 2s var(--slide-transition);

    &.swiper-slide-active {
      opacity: 1;
      transform: scale(1);
      z-index: 10;

      .clients__slider-image {
        cursor: pointer;
      }
    }

    &.swiper-slide-next,
    &.swiper-slide-prev {
      transform: scale(0.6);
    }
  }

  &__slider-image-wrapper {
    position: relative;
    // width: clamp(120px, 18vw, 180px);
    // min-height: clamp(50px, 10vw, 100px);
    width: clamp(140px, 20vw, 200px); // увеличено на десктопе +20px
    min-height: clamp(60px, 12vw, 110px); 
    display: flex;
    align-items: center;
    justify-content: center;
    isolation: isolate;
  }

  &__slider-image-left,
  &__slider-image-right {
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 150px;
    transform-style: preserve-3d;
  }

  &__slider-image-left {
    perspective-origin: left center;
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);

    .clients__slider-image {
      animation: image-left 1s ease-in-out infinite alternate;
    }
  }

  &__slider-image-right {
    perspective-origin: right center;
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
    margin-left: -0.55px;

    .clients__slider-image {
      animation: image-right 1s ease-in-out infinite alternate;
    }
  }

  &__slider-image {
    max-height: clamp(60px, 6vw, 92px);
    // max-height: clamp(50px, 6vw, 82px);
    width: auto;
    object-fit: contain;
    backface-visibility: hidden;
    pointer-events: auto;
    user-select: none;
    touch-action: manipulation;
  }

  &__facts {
    width: 100%;
    margin-bottom: fluid(60, 20);
  }

  &__reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
  } 
}

